// ** React Imports
import { Fragment, useState, useEffect } from 'react'
// ** Table Columns
import { columns } from './columns'

// ** Store & Actions
import { getSales, exportSales } from '../store/index'
import { useDispatch, useSelector } from 'react-redux'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import ReactPaginate from 'react-paginate'
import DataTable from 'react-data-table-component'
import { ChevronDown, Download, MoreVertical } from 'react-feather'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
// ** Reactstrap Imports
import {
    Row,
    Col,
    Card,
    Input,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Button
} from 'reactstrap'
import Select from 'react-select'
import { useParams } from 'react-router-dom'
import moment from 'moment'

const CustomHeader = ({ handlePerPage, rowsPerPage, handleFilter, searchTerm, exportList, applyFileter, dataId }) => {
    // ** Converts table to CSV

    const options = [
        { id: 1, name: 'All Time', startDate: '', endDate: '' },
        { id: 2, name: 'This Month', startDate: moment().startOf('M').valueOf(), endDate: moment().endOf('M').valueOf() },
        { id: 3, name: 'Last Month', startDate: moment().subtract(1, 'M').startOf('M').valueOf(), endDate: moment().subtract(1, 'M').endOf('M').valueOf() },
        { id: 6, name: 'This Year', startDate: moment().set('M', 3).startOf('M').valueOf(), endDate: moment().set('M', 2).add(1, 'Y').endOf('M').valueOf() },
        { id: 8, name: 'Custom Range', startDate: '', endDate: '' }
    ]

    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')
    const [selectOpt, setSelectOpt] = useState(1)

    const onDateChange = dates => {
        const [start, end] = dates
        setStartDate(start)
        setEndDate(end)

        if (start && end) {
            applyFileter({ id: selectOpt, startDate: moment(start).valueOf() || '', endDate: moment(end).valueOf() || "", flag: true })
        }
    }

    const resetFilter = () => {
        setStartDate('')
        setSelectOpt('')
        setEndDate('')
        applyFileter({ id: 1, name: 'All Time', startDate: '', endDate: "", flag: true })
    }
    return (
        <div className='invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75'>
            <Row>
                <Col xl='6' md='6' sm='6' className='d-flex align-items-center p-0'>
                    <div className='d-flex align-items-center w-100'>
                        <label htmlFor='rows-per-page'>Show</label>
                        <Input
                            className='mx-50'
                            type='select'
                            id='rows-per-page'
                            value={rowsPerPage}
                            onChange={handlePerPage}
                            style={{ width: '5rem' }}
                        >
                            <option value='10'>10</option>
                            <option value='25'>25</option>
                            <option value='50'>50</option>
                        </Input>
                        <label htmlFor='rows-per-page'>Entries</label>
                        <Col className='me-1'>
                            <Select
                                className={'react-select ms-1 w-100'}
                                classNamePrefix='select'
                                options={options}
                                onChange={val => { applyFileter(val); setSelectOpt(val.id) }}
                                getOptionLabel={(option) => option.name}
                                getOptionValue={(option) => option.id}
                                value={options.find(oj => oj.id === selectOpt) || {}}
                            />
                        </Col>

                        {
                            (dataId === 8) &&
                            <Col className={'ms-1'}>
                                <Flatpickr
                                    onChange={(date) => { onDateChange(date) }}
                                    startdate={startDate}
                                    selected={startDate}
                                    enddate={endDate}
                                    options={{ mode: 'range', altInput: true, altFormat: "M j, Y", dateFormat: "M j, Y" }}
                                    className='form-control invoice-edit-input date-picker'
                                />
                            </Col>
                        }
                        <Button outline color='primary' size='sm' className='ms-1' onClick={() => { resetFilter() }}>Clear Filter</Button>
                    </div>
                </Col>
                <Col
                    xl='6'
                    className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
                >
                    <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
                        <label className='mb-0' htmlFor='search-invoice'>
                            Search:
                        </label>
                        <Input
                            id='search-invoice'
                            className='ms-50 w-100'
                            type='text'
                            value={searchTerm}
                            onChange={e => handleFilter(e.target.value)}
                        />
                    </div>

                    <div className='d-flex align-items-center table-header-actions'>

                        <UncontrolledDropdown className='ms-1'>
                            <DropdownToggle color='secondary' caret outline>
                                <MoreVertical className='font-small-4 me-50' />
                            </DropdownToggle>
                            <DropdownMenu>
                                <DropdownItem onClick={() => { exportList() }} className='w-100'>
                                    <Download className='font-small-4 me-50' />
                                    <span className='align-middle'>Export</span>
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>

                    </div>
                </Col>
            </Row>
        </div>
    )
}

const SalesReport = () => {
    // ** Store Vars
    const dispatch = useDispatch()
    const { option } = useParams()
    const store = useSelector(state => state.salesreport)
    // ** States
    const [dataId, setDataId] = useState(2)
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')
    const [sort, setSort] = useState('desc')
    const [searchTerm, setSearchTerm] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [sortColumn, setSortColumn] = useState('createdon')
    const [rowsPerPage, setRowsPerPage] = useState(10)


    // ** Get data on mount
    useEffect(async () => {
        await dispatch(
            getSales({
                sort,
                sortColumn,
                q: searchTerm,
                page: currentPage,
                perPage: rowsPerPage,
                option,
                startDate,
                endDate
            })
        )
    }, [dispatch])

    // ** Function in get data on page change
    const handlePagination = async page => {
        await dispatch(
            getSales({
                sort,
                sortColumn,
                q: searchTerm,
                perPage: rowsPerPage,
                option,
                page: page.selected + 1,
                startDate,
                endDate
            })
        )
        setCurrentPage(page.selected + 1)
    }

    // ** Function in get data on rows per page
    const handlePerPage = async e => {
        const value = parseInt(e.currentTarget.value)
        await dispatch(
            getSales({
                sort,
                sortColumn,
                q: searchTerm,
                perPage: value,
                option,
                page: currentPage,
                startDate,
                endDate
            })
        )
        setRowsPerPage(value)
    }

    const exportList = async () => {
        await dispatch(
            exportSales({
                sort,
                sortColumn,
                q: searchTerm,
                option,
                export: true,
                startDate,
                endDate
            })
        )
    }

    // ** Function in get data on search query change
    const handleFilter = async val => {
        setSearchTerm(val)
        await dispatch(
            getSales({
                sort,
                q: val,
                sortColumn,
                page: currentPage,
                perPage: rowsPerPage,
                option,
                startDate,
                endDate
            })
        )
    }

    const applyFileter = async (val) => {

        setDataId(val.id)

        if (val.id === 1 && !val.flag) {
            return
        }
        setStartDate(val.startDate)
        setEndDate(val.endDate)
        await dispatch(
            getSales({
                sort,
                q: searchTerm,
                sortColumn,
                page: currentPage,
                perPage: rowsPerPage,
                option,
                startDate: val.startDate,
                endDate: val.endDate
            })
        )
    }
    // ** Custom Pagination
    const CustomPagination = () => {
        const count = Number(Math.ceil(store.total / rowsPerPage))

        return (
            <ReactPaginate
                previousLabel={''}
                nextLabel={''}
                pageCount={count || 1}
                activeClassName='active'
                forcePage={currentPage !== 0 ? currentPage - 1 : 0}
                onPageChange={page => handlePagination(page)}
                pageClassName={'page-item'}
                nextLinkClassName={'page-link'}
                nextClassName={'page-item next'}
                previousClassName={'page-item prev'}
                previousLinkClassName={'page-link'}
                pageLinkClassName={'page-link'}
                containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
            />
        )
    }

    // ** Table data to render
    const dataToRender = () => {
        const filters = {
            q: searchTerm
        }

        const isFiltered = Object.keys(filters).some(function (k) {
            return filters[k].length > 0
        })

        if (store.sales.length > 0) {
            return store.sales
        } else if (store.sales.length === 0 && isFiltered) {
            return []
        }
    }

    const handleSort = async (column, sortDirection) => {
        setSort(sortDirection)
        setSortColumn(column.sortField)
        await dispatch(
            getSales({
                sort,
                sortColumn,
                q: searchTerm,
                page: currentPage,
                perPage: rowsPerPage,
                option,
                startDate,
                endDate
            })
        )
    }

    return (
        <Fragment>
            <Card className='overflow-hidden'>
                <div className='react-dataTable'>
                    <DataTable
                        noHeader
                        subHeader
                        sortServer
                        pagination
                        responsive
                        paginationServer
                        columns={columns}
                        onSort={handleSort}
                        sortIcon={<ChevronDown />}
                        className='react-dataTable'
                        paginationComponent={CustomPagination}
                        data={dataToRender()}
                        subHeaderComponent={
                            <CustomHeader
                                store={store}
                                searchTerm={searchTerm}
                                rowsPerPage={rowsPerPage}
                                handleFilter={handleFilter}
                                handlePerPage={handlePerPage}
                                exportList={exportList}
                                applyFileter={applyFileter}
                                dataId={dataId}
                            />
                        }
                    />
                </div>
            </Card>
        </Fragment>
    )
}

export default SalesReport
