// ** React Imports
import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'
import moment from 'moment'
import { Badge } from 'reactstrap'

import { indianFormattedPrice } from '@src/helper/functionHelper.js'
// ** Renders Client Columns
const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.contactname ? row.contactname?.charAt(0) : 'T'}
    />
  )
}
const paymentstatusObj = [
  { id: '', name: "Select All" },
  { id: '5', name: "Pending", color: 'light-danger' },
  { id: '6', name: "Partially Paid", color: 'light-warning' },
  { id: '7', name: 'Paid', color: 'light-success' },
  { id: '11', name: 'Sent', color: 'light-secondary' }
]

const dateFormat = (val) => {
  return moment(val, 'x').format('MMM DD, Y')
}

const renderStatus = (val) => {
  const obj = paymentstatusObj.find((o) => parseInt(o.id) === parseInt(val))
  return obj
}

export const columns = [
  {
    name: 'Unique Id',
    sortable: true,
    minWidth: '108px',
    selector: row => row.uniqueno,
    cell: row => <span className='text-capitalize'>{row.uniqueno}</span>
  },
  {
    name: 'Client',
    sortable: true,
    minWidth: '250px',
    sortField: 'client',
    selector: row => row.id,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center text-overflow-ellipsis'>
        {renderClient(row)}
        <div className='d-flex flex-column'>
          <span className='fw-bolder'>{row.contactname}</span>
          <span >{row.contactemail}</span>
        </div>
      </div>
    )
  },
  {
    name: 'Service',
    sortable: true,
    minWidth: '170px',
    sortField: 'servciename',
    selector: row => row.servicename
  },  
  {
    name: 'INV Date',
    sortable: true,
    minWidth: '88px',
    selector: row => row.invoicedate,
    cell: row => <span className='text-capitalize'>{dateFormat(row.invoicedate)}</span>
  },
  {
    name: 'INV Amount',
    sortable: true,
    minWidth: '88px',
    selector: row => row.totalamount,
    cell: row => <span className='text-capitalize'>{indianFormattedPrice(row.totalamount)}</span>
  },
  {
    name: 'GST',
    sortable: true,
    minWidth: '88px',
    selector: row => row.totaltaxamount,
    cell: row => <span className='text-capitalize'>{indianFormattedPrice(row.totaltaxamount)}</span>
  },
  {
    name: 'TDS',
    sortable: true,
    minWidth: '88px',
    selector: row => row.totaltdsamount,
    cell: row => <span className='text-capitalize'>{indianFormattedPrice(row.totaltdsamount || 0)}</span>
  },
  {
    name: 'Paid',
    sortable: true,
    minWidth: '88px',
    selector: row => row.totalpaidamount,
    cell: row => <span className='text-capitalize'>{indianFormattedPrice(row.totalpaidamount || 0)}</span>
  },
  {
    name: 'Balance',
    sortable: true,
    minWidth: '88px',
    selector: row => row.dueamount,
    cell: row => <span className='text-capitalize'>{indianFormattedPrice(row.dueamount)}</span>
  },
  {
    name: 'Status',
    sortable: true,
    minWidth: '108px',
    selector: row => row.paymentstatus,
    cell: row => <Badge color={renderStatus(row.paymentstatus)?.color || 'light-primary'}>{renderStatus(row.paymentstatus)?.name || row.paymentstatus}</Badge>
  }

]
